import UserModel from '@/models/user.js'
import PostModel from '@/models/job.js'
import ListItem from '@/components/ListItem/ListItem.vue'
const userModel = new UserModel() 
const postModel = new PostModel()
export default {
	data(){
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			postList: [],
			pageNum: 1,
			pageSize: 8,
			loading: false, 
			hasEnd: false, // 是否已加载完成
			keyword: '',
			label:'',
		}
	}, 
	components:{
		ListItem
	},
	mounted() {
		this.getJobList()
	},
	methods:{
		getJobList(){
			const that = this
			if(this.pageNum === 1){
				uni.showLoading({
					mask: true
				})
			}
			postModel.jobList(this.pageNum, this.pageSize, this.keyword, this.label).then(res=>{
				const { code, message, data } = res.data
				uni.hideLoading()
				uni.stopPullDownRefresh()
				if(code === '0'){
					if(this.pageNum === 1){
						that.postList = data
					}else {
						that.postList = [...that.postList, ...data]
					}
					if(data.length < that.pageSize){
						that.hasEnd = true
					}
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				uni.showToast({
					icon: 'none',
					title:'获取岗位列表信息失败'
				})
			})
		},
		refresh(){
			this.pageNum = 1
			this.hasEnd = false
			this.label = ''
			this.getJobList()
		},
		getNext(){
			if(!this.hasEnd){
				this.pageNum += 1
				this.getJobList()
			}
		},
		showDetail(id){
			const that = this
			uni.checkSession({
				success(res) {
					const token = uni.getStorageSync('token')
					const isLogin = uni.getStorageSync('isLogin')
					const isRegister = uni.getStorageSync('isRegister')
					if(token && isLogin === 0 && isRegister === 0){
						uni.navigateTo({
							url: '../../public/PostDetail/PostDetail?id='+id
						})
					}else if(token && isLogin === 0){
						uni.navigateTo({
							url: '../../user/inputInfo/inputInfo'
						})
					}else{
						that.selectLoginType()
					}
				},
				fail(err) {
					that.selectLoginType()
				}
			})
		},
		switchToSeller(){
			// 判断该用户是否存在b
			
			// 存在B端用户信息时
			uni.reLaunch({
				url: '../../seller/main/main'
			})
		},
		switchLabel(label){
			this.label = label
			this.pageNum = 1
			this.hasEnd = false
			this.getJobList()
		}
	}
}
